.menu {
  position: fixed;
  top: 0vw;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(211, 211, 211, 0.5);
  display: none;
  transition: 1s;
  border-radius: 12px;
}
.menu .menu-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.menu .menu-wrapper .login-wrapper {
  background: url(../../assets/img/login-bg.png);
  width: 50%;
  height: 60%;
  background-size: 100%;
  display: flex;
  justify-content: right;
}
.menu .menu-wrapper .login-wrapper .login-block {
  width: 50%;
  background: #FFFFFF;
  border-radius: 85px 0px 0px 85px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu .menu-wrapper .login-wrapper .login-block .login-block-wrapper {
  width: 80%;
  margin: auto;
  margin-top: 25%;
}
.menu .menu-wrapper .login-wrapper .login-block .login-block-wrapper h1 {
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  color: #252C33;
}
.menu .menu-wrapper .login-wrapper .login-block .login-block-wrapper form input {
  border: none;
  border-bottom: 1px solid #9F9F9F;
  width: 100%;
  height: 50%;
  margin-top: 10%;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  color: #5A5A5A;
}
.menu .menu-wrapper .login-wrapper .login-block .login-block-wrapper form #login-btn {
  background: #D3AF37;
  border-radius: 10px;
  border: none;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  color: #000000;
  width: 40%;
  height: 3vw;
}
.menu .menu-wrapper .login-wrapper .login-block h2 {
  color: #2bc559;
  font-style: normal;
  font-weight: 900;
  font-size: 32px;
}

.menu.active {
  display: block;
}/*# sourceMappingURL=Login.css.map */