.contacts {
  display: flex;
  height: 40vw;
}
.contacts .iframe-block {
  width: 50%;
  height: 100%;
}
.contacts .contacts-info-block {
  width: 50%;
}
.contacts .contacts-info-block .contacts-info-wrapper {
  width: 80%;
  margin: auto;
}
.contacts .contacts-info-block .contacts-info-wrapper h1 {
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  color: #000000;
}
.contacts .contacts-info-block .contacts-info-wrapper .contacts-info-block-item {
  display: flex;
  align-items: center;
}
.contacts .contacts-info-block .contacts-info-wrapper .contacts-info-block-item p {
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  color: #252C33;
}
.contacts .contacts-info-block .contacts-info-wrapper form {
  width: 100%;
}
.contacts .contacts-info-block .contacts-info-wrapper form .contacts-inp {
  width: 100%;
  height: 2vw;
  margin-top: 3%;
  background: #FFFFFF;
  border: 1px solid #A1B5C0;
  border-radius: 5px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  color: #616161;
}
.contacts .contacts-info-block .contacts-info-wrapper form .message-inp {
  width: 100%;
  height: 10vw;
  margin-top: 3%;
  background: #FFFFFF;
  border: 1px solid #A1B5C0;
  border-radius: 5px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  color: #616161;
}
.contacts .contacts-info-block .contacts-info-wrapper form .submit-inp {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  color: #252C33;
  background: #D3AF37;
  border-radius: 10px;
  border: none;
  height: 2vw;
  width: 20%;
  margin-top: 3%;
}/*# sourceMappingURL=Contacts.css.map */